import styled from 'styled-components'
import Color from 'color'

export const Link = styled.a`
  color: ${(props) => props.color};
  text-decoration: none;

  &:visited {
    color: ${(props) => props.color};
  }

  &:hover {
    color: ${(props) => Color(props.color).saturate(0.2)};
  }

  &:active {
    color: ${(props) => Color(props.color).desaturate(0.2)};
  }

  &:focus {
    color: ${(props) => Color(props.color).saturate(0.2)};
  }
`

Link.defaultProps = {
  color: '#38c9e4'
}
